/**
 * Component for footer
 */

footer {
  position: relative;
  padding-top: remy(64px);
  padding-bottom: remy(48px);
  background-color: palette-grabber($palette-material, grey, 800);

  .container {
    position: relative;
  }

  .btn--fab {
    position: absolute;
    top: remy(-88px);
    right: remy(16px);
    margin: auto;

    @media screen and (min-width: 790px) {
      right: 0;
    }
  }
}

.nav--social {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: remy(24px);
  font-size: remy(20px);

  .list-break {
    margin-top: 6px;
    margin-bottom: 6px;
    width: 100%;
    height: 1px;

    @media screen and (min-width: 450px) {
      display: none;
    }
  }

  li {
    &:nth-of-type(n+2) {
      &:not(.list-break) {
        margin-left: remy(16px);
      }
    }
  }

  a {
    display: block;
    min-width: remy(34px);
    font-size: remy(26px);
    text-align: center;

    &:focus,
    &:hover {
      color: rgba(#86878b, .35);
    }
  }
}

.footer__copy {
  color: rgba(255, 255, 255, .5);
}
