/**
 * Typography
 */

a {
  text-decoration: none;

  @include transition(all);

  &:focus,
  &:hover {
    text-decoration: none;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Raleway', 'Open Sans', 'Trebuchet MS', sans-serif;
}

ul {
  padding: 0;
  list-style-type: none;
}
