/**
 * Homepage - index-countdown
 */

.page-countdown {
  .navbar-header,
  .navbar-brand {
    float: none;
    margin: 0;
    text-align: center;
  }

  .navbar-header {
    text-align: center;
  }

  .hero--particlesjs {
    .countdown {
      > div,
      span {
        color: #212121;
      }

      span {
        &:before {
          background: #212121;
        }
      }
    }
  }
}

.hero__overlay {
  br {
    @media (max-width: 439px),
      (min-width: 576px) and (max-width: 767px) {
        display: none;
    }
  }

  .nav--social {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
  }

  .link--default {
    color: #fff;
    opacity: .85;
  }
}
