/**
 * Forms molecule
 */

.form-group {
  position: relative;
  padding-left: 0;
  margin-bottom: remy(32px);

  &:first-of-type {
    @media screen and (max-width: 550px) {
      margin-bottom: remy(32px);
    }
  }
}

label,
.form-label {
  position: absolute;
  top: remy(8px);
  left: 0;
  z-index: 1;
  margin-bottom: 0;
  display: block;
  font-size: remy(14px);
  font-weight: 400;
  color: palette-grabber($palette-material, grey, 600);

  @include transition(all);
}

input,
select,
textarea,
.form-element {
  position: relative;
  z-index: 3;
  display: block;
  padding-bottom: remy(8px);
  margin-bottom: remy(8px);
  width: 100%;
  font-family: $font--open-sans;
  background-color: transparent;
  border-top: 0;
  border-right: 0;
  border-bottom: 1px solid palette-grabber($palette-material, grey, 300);
  border-left: 0;

  &:focus {
    outline: 0;
    border-bottom-color: $color--primary;
  }
}

.form-element {
  &:focus + label,
  &:focus + .form-label {
    font-size: remy(12px);
    font-weight: 700;
    color: $color--primary;
    transform: translateY(-26px);
  }
}

.form {
  textarea {
    resize: vertical;
  }
}

.form--contact {
  textarea {
    min-height: remy(100px);

    @media screen and (min-width: 768px) {
      min-height: remy(195px);
    }
  }
}

.form--hero {
  margin-top: 16px;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  max-width: remy(255px);
}

.form--optin {
  margin-right: auto;
  margin-left: auto;
  max-width: remy(480px);

  .form-group {
    position: relative;
  }

  input {
    padding-top: remy(15px);
    padding-bottom: remy(15px);
    padding-right: remy(28px);
    padding-left: remy(28px);
    background-color: #fff;
    border: 1px solid #fff;

    @include box-shadow--stripe;

    &:focus {
      border: 1px solid $color--primary;
    }

    @media screen and (min-width: 440px) {
      padding-right: remy(188px);
    }
  }

  .btn {
    @media screen and (max-width: 439px) {
      width: 100%;

      @include box-shadow--stripe;
    }

    @media screen and (min-width: 440px) {
      position: absolute;
      top: remy(3px);
      right: remy(4px);
      z-index: 3;
      box-shadow: none;
    }
  }
}
