// Template options panel

.template-options-panel {
  position: fixed;
  top: 50%;
  left: -220px;
  z-index: 5;
  padding: remy(12px);
  width: remy(220px);
  height: auto;
  min-height: remy(150px);
  text-align: left;
  background-color: #ededed;
  border-top-right-radius: remy(4px);
  border-bottom-right-radius: remy(4px);
  transform: translateY(-50%);
  transition: left .5s ease-in-out;

  select {
    font-size: 14px;
  }

  .switch,
  .theme {
    display: inline-block;
    margin-top: remy(6px);
    margin-right: remy(3px);
    width: remy(24px);
    height: remy(24px);
    cursor: pointer;
  }
}

.template-panel-open {
  left: 0;
}

.template__button {
  position: absolute;
  top: remy(35px);
  right: remy(-67px);
  width: remy(95px);
  height: remy(40px);
  font-size: remy(14px);
  font-weight: 400;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  //background-color: #212121;
  border-width: 1px;
  border-style: solid;
  border-bottom-right-radius: remy(4px);
  border-bottom-left-radius: remy(4px);
  cursor: pointer;
  transform: rotate(-90deg);
  @include transition(all);

  &:hover {
    /*background-color: #666;
    border-color: #666;*/
  }

  &:focus {
    outline: 0;
  }
}

