/**
 * Navigation molecule
 */
$header-height: remy(65px);

.website-header {
  background: rgba(255, 255, 255, .98);
  box-shadow: $box-shadow;
}

.navbar-trinity {
  padding: 0;
  background: rgba(255, 255, 255, .98);
  border-width: 0;

  .navbar-brand {
    padding-top: 0;
    padding-bottom: 0;
    height: auto;
    font-size: remy(18px);
    font-weight: 700;
    line-height: $header-height;
    color: palette-grabber($palette-material, grey, 600);

    &:focus,
    &:hover {
      opacity: .5;
    }
  }

  .navbar-toggler {
    right: 0;
    padding: 0 remy(16px);
    margin: 0;
    height: $header-height;
    border: 0;
    cursor: pointer;

    &:focus,
    &:hover {
      background: transparent;

      .icon-bar {
        background-color: $color--primary;
      }
    }

    .icon-bar {
      display: block;
      width: remy(22px);
      height: remy(2px);
      background-color: palette-grabber($palette-material, grey, 600);
      border-radius: 1px;

      @include transition(background-color);

      & + & {
        margin-top: remy(4px);
      }
    }
  }

  .navbar-nav {
    .nav-link {
      display: block;
      font-size: remy(14px);
      color: $color--grey-default;
      border: 0;
      text-transform: uppercase;

      &:focus,
      &:hover {
        color: $color--primary;
        border-bottom-color: $color--primary;
        background: transparent;
      }

      @media (max-width: 767px) {
        text-align: right;

        &:focus,
        &:hover {
          color: $color--primary;
        }
      }

      @media screen and (min-width: 768px) {
        padding-top: 0;
        padding-right: remy(10px);
        padding-bottom: 0;
        padding-left: remy(10px);
        line-height: 63px;// + 2px border-bottom = 65px
        border-bottom: 2px solid #fff;

        &:focus,
        &:hover {
          border-bottom-color: $color--primary;
        }
      }

      @media screen and (min-width: 992px) {
        padding-right: remy(18px);
        padding-left: remy(18px);
      }
    }
  }

  .navbar-collapse {
    border: 0;
  }
}
