/**
 * Slider molecule
 */

.trinity-slider__wrapper,
.trinity-slider {
  display: flex;
}

.trinity-slider__wrapper {
  align-items: center;
  flex-wrap: wrap;

  > .col-sm-3 {
    @media screen and (max-width: 767px) {
      width: 100%;
    }
  }
}

ul {
  &.trinity-slider {
    flex-direction: column;
    overflow: hidden;
    border-top-left-radius: $radius--small;
    border-bottom-left-radius: $radius--small;

    @media screen and (max-width: 767px) {
      flex-direction: row;
      justify-content: center;
      overflow: visible;
      margin-bottom: remy(32px);
    }
  }
}

.slider__link {
  position: relative;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  width: remy(90px);
  height: remy(90px);
  font-size: remy(12px);
  font-weight: bold;
  color: #fff;
  text-transform: uppercase;
  background-color: palette-grabber($palette-material, blue grey, 700);

  &:focus,
  &:hover {
    color: #fff;
    background-color: lighten($color--primary, 7%);

    &:before {
      border-left-color: lighten($color--primary, 7%);
    }
  }

  &:before {
    position: absolute;
    top: remy(45px);
    right: remy(-10px);
    content: '';
    border-top: remy(10px) solid transparent;
    border-right: 0 solid transparent;
    border-bottom: remy(10px) solid transparent;
    border-left: remy(10px) solid palette-grabber($palette-material, blue grey, 700);

    @include transition(all);

    @media screen and (max-width: 767px) {
      top: remy(154px);
      right: 0;
      left: 0;
      transform: rotate(90deg);
    }

    @media screen and (max-width: 449px) {
      top: remy(125px);
    }
  }

  @media screen and (min-width: 450px) {
    width: remy(110px);
    height: remy(110px);
  }
}

.slider__link--active {
  background-color: $color--primary;

  &:focus,
  &:hover {
    background-color: darken($color--primary, 7%);

    &:before {
      border-left-color: darken($color--primary, 7%);
    }
  }

  &:before {
    border-left-color: $color--primary;
  }
}

.slider__icon {
  margin-bottom: remy(-35px);
  width: 100%;
  font-size: 1.85rem;
  text-align: center;
}

.slider__slide {
  display: none;

  > .row {
    @media screen and (max-width: 767px) {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column-reverse;
    }
  }
}

.slider__slide--active {
  display: block;
}

.slide__heading {
  margin-top: 0;
}

.slide__mockup {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;

  @media screen and (max-width: 767px) {
    margin-top: remy(64px);
  }

  & > div {
    align-self: center;
  }

  img {
    display: block;
    margin-right: auto;
    margin-left: auto;
    max-width: 100%;
    max-height: remy(300px);
  }
}
