/**
 * Countdown molecule
 */

.countdown {
  display: table;
  margin: 0 auto remy(24px);
  font-size: remy(28px);
  font-weight: 500;

  > div {
    float: left;
    min-width: remy(80px);
    color: #fff;

    @media screen and (max-width: 369px) {
      &:nth-of-type(3) {
        clear: left;
      }
    }
  }

  span {
    position: relative;
    display: block;
    padding-top: remy(2px);
    margin-top: remy(6px);
    font-size: remy(16px);
    color: #fff;
    text-align: center;

    &:before {
      position: absolute;
      top: remy(-4px);
      right: 0;
      left: 0;
      margin-right: auto;
      margin-left: auto;
      width: remy(20px);
      height: remy(1px);
      content: '';
      background: #fff;
    }
  }
}
