/**
 * Inserts molecule
 */

.insert {
  position: relative;
}

.insert--logos {
  padding-top: remy(28px);
  padding-bottom: remy(28px);

  ul {
    margin-bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
    overflow: hidden;

    @media screen and (min-width: 768px) {
      justify-content: space-between;
    }
  }

  @media screen and (max-width: 767px) {
    justify-content: space-around;

    li {
      &:nth-of-type(even) {
        display: none;
      }
    }
  }
}

.insert__logo {
  width: 100%;
  max-width: remy(150px);
  max-height: remy(55px);

  @media screen and (min-width: 768px) {
    max-width: remy(100px);
    max-height: remy(25px);
  }

  @media screen and (min-width: 992px) {
    max-width: remy(120px);
    max-height: remy(35px);
  }

  path {
    fill: #bfbfbf;
  }
}

.insert--cta {
  height: remy(320px);
  color: #fff;
  background-image: $insert-cta-bcg;

  @include retina {
    background-image: $insert-cta-retina-bcg;
  }

  & > .container {
    height: 100%;
  }
}

.cta__content {
  z-index: 4;

  @include vertical-align(relative);
}

.insert--map {
  position: relative;
  height: remy(320px);
}

.insert--media {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.insert--overlay {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
}

.insert--shadow {
  @include box-shadow--stripe(1);
}
