/**
 * Base
 */

html,
body {
  width: 100%;
  height: 100%;
}

// sass-lint:disable no-vendor-prefixes
html {
  font-size: 16px;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}
// sass-lint:enable no-vendor-prefixes

body {
  font-family: $font--open-sans;
  font-size: 100%;
  font-weight: 400;
  color: $color--text;
  line-height: 1.45;
}

/* Avoid empty images to appear as broken */
img {
  &:not([src]) {
    &:not([srcset]) {
      visibility: hidden;
    }
  }
}
