///
// Mixins
///

// Mixin for box-shadow - default
@mixin box-shadow--default($level: 1) {
  @if $level == 1 {
    box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 2px rgba(0, 0, 0, .24);
  } @else if $level == 2 {
    box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
  } @else if $level == 3 {
    box-shadow: 0 10px 20px rgba(0, 0, 0, .19), 0 6px 6px rgba(0, 0, 0, .23);
  } @else if $level == 4 {
    box-shadow: 0 14px 28px rgba(0, 0, 0, .25), 0 10px 10px rgba(0, 0, 0, .22);
  } @else if $level == 5 {
    box-shadow: 0 19px 38px rgba(0, 0, 0, .3), 0 15px 12px rgba(0, 0, 0, .22);
  }
}

// Mixin for box-shadow inspired by stripe.com
@mixin box-shadow--stripe($level: 1) {
  @if $level == 1 {
    box-shadow: 0 15px 35px rgba(50, 50, 93, .1), 0 5px 15px rgba(0, 0, 0, .07);
  } @else if $level == 2 {
    box-shadow: 0 26px 35px rgba(50, 50, 93, .1), 0 14px 15px rgba(0, 0, 0, .07);
  }
}

// Mixins for retina screens
// sass-lint:disable no-vendor-prefixes
@mixin retina {
  @media only screen and (-webkit-min-device-pixel-ratio: 2),
    only screen and (min--moz-device-pixel-ratio: 2),
    only screen and (min-device-pixel-ratio: 2),
    only screen and (min-resolution: 192dpi),
    only screen and (min-resolution: 2dppx) {
      @content;
  }
}
// sass-lint:enable no-vendor-prefixes

// Mixin for transition
@mixin transition($prop, $duration: .25s, $timing: cubic-bezier(.4, 0, 1, 1)) {
  transition: $prop $duration $timing;
}

// Mixin for vertical centering
@mixin vertical-align($position: absolute) {
  position: $position;
  top: 50%;
  transform: translateY(-50%);
}
