/**
 * Features molecule
 */

.features__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;

  & > div {
    @media screen and (max-width: 767px) {
      margin-bottom: remy(16px);
    }
  }

  p {
    @media screen and (max-width: 767px) {
      margin-right: auto;
      margin-left: auto;
    }
  }
}

.features__mockup {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;

  img {
    max-width: 100%;
  }
}

.features__icon {
  padding: remy(12px) remy(13px);
  color: #fff;
  background-color: $color--primary;
  border-radius: 50%;
}
