/**
 * Buttons atom
 */

.btn__container {
  text-align: center;
}

.btn {
  font-size: remy(14px);
  text-transform: uppercase;
  border-radius: $radius--small;
  cursor: pointer;

  @include box-shadow--stripe;

  @include transition(all);

  &:active,
  &:focus,
  &:hover {
    color: #fff;

    @include box-shadow--stripe(2);
  }
}

.btn--fab {
  display: block;
  font-size: remy(24px);
  color: #fff;
  text-align: center;
  border-radius: 50%;
}

.btn--fab--primary {
  background-color: $color--primary;

  &:focus,
  &:hover {
    background-color: lighten($color--primary, 7%);
  }

  &:active {
    background-color: darken($color--primary, 7%);
  }
}

.btn--fab--big {
  padding: 1rem;
  width: remy(56px);
  height: remy(56px);
}

.btn--fab--medium {
  padding: .35rem;
  width: remy(48px);
  height: remy(48px);
  font-size: remy(24px);
}

.btn--fab--small {
  padding: .35rem;
  width: remy(40px);
  height: remy(40px);
}

.btn--full,
.btn--ghost {
  padding: remy(12px) remy(42px);
  border: 2px solid;
  font-weight: 700;
}

.btn--full--black {
  $color: $color--black-medium;

  color: #fff;
  background-color: $color;
  border-color: $color;

  &:focus,
  &:hover {
    background-color: lighten($color, 15%);
    border-color: lighten($color, 15%);
  }
}

.btn--full--primary {
  $color: $color--primary;

  color: #fff;
  background-color: $color;
  border-color: $color;

  &:focus,
  &:hover {
    background-color: lighten($color, 15%);
    border-color: lighten($color, 15%);
  }
}

.btn--full--white {
  $color: #fff;

  color: #212121;
  background-color: $color;
  border-color: $color;

  &:focus,
  &:hover {
    color: lighten(#212121, 15%);
  }
}

.btn--ghost {
  background: transparent;
}

.btn--ghost--black {
  $color: $color--black-medium;

  color: $color;
  border-color: $color;

  &:focus,
  &:hover {
    color: lighten($color, 15%);
    border-color: lighten($color, 15%);
  }
}

.btn--ghost--primary {
  $color: $color--primary;

  color: $color;
  border-color: $color;

  &:focus,
  &:hover {
    color: lighten($color, 15%);
    border-color: lighten($color, 15%);
  }
}

.btn--ghost--white {
  color: #fff;

  &:focus,
  &:hover {
    color: $color--secondary;
  }
}

.btn--round {
  border-radius: $radius--big;
}
