/**
 * Links atom
 */

.link--default {
  color: $color--text-light;

  &:focus,
  &:hover {
    color: $color--text;
  }
}

.link--black {
  color: $color--text;

  &:focus,
  &:hover {
    color: $color--secondary;
  }
}

.link--gold {
  color: $color--secondary;

  &:focus,
  &:hover {
    color: $color--text;
  }
}

.link--white {
  color: #fff;

  &:focus,
  &:hover {
    color: $color--grey-default;
  }
}
