/**
 * Heroes molecule
 */

.hero {
  position: relative;
  background-position: center center;
  background-size: cover;

  @media screen and (min-width: 768px) {
    height: 100vh;
    min-height: remy(640px);
  }
}

.hero--gradient {
  background: #56ccf2;/* fallback for old browsers */
  background: linear-gradient(to top, #56ccf2, #2f80ed);// sass-lint:disable-line no-duplicate-properties
  /* uncomment the lines below to turn on animated gradient */
  /* background-size: 400% 400%;
  animation: gradient-animation 15s ease infinite;*/

  .hero__overlay {
    background-color: rgba(0, 0, 0, .01);
  }
}

.hero--image {
  background-image: $hero-home-bcg;

  @include retina {
    background-image: $hero-home-retina-bcg;
  }
}

.hero--optin {
  min-height: remy(800px);
  height: 100vh;

  @media screen and (min-width: 540px) {
    min-height: remy(540px);
  }
}

.hero--home {
  min-height: remy(1136px);

  @media screen and (min-width: 555px) {
    min-height: remy(960px);
  }
}

.hero__overlay {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .45);
}

.hero__content {
  width: 100%;

  @include vertical-align(relative);

  .list {
    align-items: center;
  }

  h1 {
    font-size: remy(48px);
    font-weight: 300;
    color: rgba(255, 255, 255, .95);
    text-transform: uppercase;

    @media screen and (max-width: 767px) {
      text-align: center;
    }
  }

  p {
    margin-bottom: 28px;
    color: rgba(255, 255, 255, .75);

    @media screen and (max-width: 767px) {
      margin-right: auto;
      margin-left: auto;
      text-align: center;
    }
  }

  .card {
    @media screen and (min-width: 768px) {
      margin-top: 0;
    }
  }
}

/* Hero - Carousel */
.hero--carousel {
  overflow: hidden;

  @keyframes fade-in {
    0% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }

  .carousel__wrapper,
  .carousel-inner,
  .item,
  .carousel__item {
    min-height: remy(1136px);
    height: 100%;

    @media screen and (min-width: 555px) {
      min-height: remy(960px);
    }
  }

  .item {
    transition: opacity .95s;

    &.active {
      animation: fade-in .95s;
    }
  }

  .carousel__item {
    width: 100%;
    height: 100%;
    background-size: cover;
  }

  .carousel__item-one {
    background: $slide-bcg-one no-repeat center center;
  }

  .carousel__item-two {
    background: $slide-bcg-two no-repeat center center;
  }

  .carousel__item-three {
    background: $slide-bcg-three no-repeat center center;
  }

  .carousel__item-four {
    background: $slide-bcg-four no-repeat center center;
  }
}

/* Hero -  ParticlesJS */
.hero--particlesjs {
  background-color: #fff;
  background-image: none;

  .hero__overlay {
    background-color: rgba(255, 255, 255, .25);
  }

  .hero__content {
    h1,
    p {
      color: #212121;
    }
  }
}

.particles {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  opacity: .3;
}

/* Hero - Video */
.hero--video {
  background-image: none;
  overflow: hidden;
}

.video-container {
  position: relative;
  top: 0;
  left: 0;
  //overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: #000;

  video {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    display: block;
    min-width: 100% !important;// sass-lint:disable-line no-important
    min-height: 100% !important;// sass-lint:disable-line no-important
    object-fit: fill;
  }
}

.hero--countdown {
  video {
    max-width: 100%;
    max-height: 100%;
  }
}
