/**
 * Testimonials molecule
 */

@media screen and (min-width: 768px) and (max-width: 992px) {
  .section--testimonials {
    .row {
      justify-content: center;
    }

    .col-md-6 {
      &:last-of-type {
        margin-top: remy(48px);
      }
    }
  }
}

.testimonial-quote__container {
  text-align: center;

  @media screen and (max-width: 767px) {
    margin-bottom: remy(48px);
  }

  h5 {
    margin-top: remy(16px);
    margin-bottom: remy(16px);
    color: palette-grabber($palette-material, grey, 500);
  }
}

.testimonial__text {
  margin-bottom: 1.5rem;
  font-size: remy(15px);
  text-align: center;
}

.testimonial__quote {
  position: relative;
  top: remy(6px);
  display: inline-block;
  font-family: serif;
  font-size: remy(33px);
  font-style: normal;
  line-height: .5;
}

.quote-left {
  margin-right: remy(4px);
}

.quote-right {
  margin-left: remy(4px);
}

.testimonial__photo {
  img {
    width: 100%;
    max-width: remy(70px);
    border-radius: 50%;
  }
}
