/**
 * Pricing containers molecule
 */

@media screen and (min-width: 768px) and (max-width: 992px) {
  .section--packages {
    .row {
      justify-content: center;
    }

    .col-md-6 {
      &:last-of-type {
        margin-top: remy(32px);
      }
    }
  }
}

.plan__container {
  padding-top: remy(32px);
  padding-bottom: remy(32px);
  display: block;
  overflow: hidden;
  background-color: #fff;
  border-radius: 4px;
  text-align: center;

  @include box-shadow--stripe;

  @include transition(box-shadow);

  @media screen and (max-width: 767px) {
    margin-bottom: remy(32px);
  }
}

.plan__head,
.plan__footer {
  padding-top: remy(16px);
  padding-bottom: remy(16px);
  color: #212121;
}

.plan__head {
  h4 {
    margin: 0;
    text-transform: uppercase;
    font-weight: 700;
  }
}

.plan__body {
  padding-top: remy(24px);
  padding-bottom: remy(24px);
}

.plan__price {
  margin-bottom: remy(48px);
  font-size: 48px;
  font-weight: 700;

  span {
    display: block;
    font-size: 13px;
  }

  @media screen and (max-width: 767px) {
    margin-right: auto;
    margin-left: auto;
  }
}

.plan__features {
  ul {
    display: flex;
    flex-direction: column;
  }

  li {
    padding-top: remy(8px);
    padding-bottom: remy(8px);
    font-size: remy(14px);
    font-weight: 700;
    text-transform: uppercase;
  }

  span {
    margin-right: 6px;
  }
}

.plan__footer {
  color: #fff;
  background-color: #fff;
}

.plan__container--highlighted {
  &,
  .plan__footer {
    background-color: $color--primary;
  }

  &,
  h4 {
    color: #fff;
  }
}
