/**
 * Cards molecule
 */

.card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 1rem 2rem;
  background-color: #fff;
  border-radius: $radius--small;

  @include box-shadow--stripe;

  &:nth-of-type(2) {
    @media screen and (max-width: 702px) {
      margin-top: 2.5rem;
    }
  }

  &:last-of-type {
    @media screen and (max-width: 1070px) {
      margin-top: 2.5rem;
    }
  }
}

.card--hero {
  margin-right: auto;
  margin-left: auto;

  @media screen and (min-width: 769px) {
    float: right;
  }
}

.card--long {
  padding: 2rem 2rem 3rem;
  width: remy(264px);
  min-height: remy(384px);
}

.card--small {
  max-width: remy(360px);
}

.card--square {
  width: 21.5rem;
  min-height: remy(264px);
}
