///
// Settings - Global
///

$box-shadow: 0 2px 40px 0 rgba(0, 0, 0, .08);//facebook.design
$radius--big: 50px;
$radius--small: 4px;
$timing: .25s;
$font--open-sans: 'Open Sans', 'Trebuchet MS', sans-serif;
$font--raleway: 'Raleway', arial, verdana, sans-serif;

// Images
// needed to make the template themable
$hero-home-bcg: url(../images/bcgs/hero.jpg) !default;
$hero-home-retina-bcg: url(../images/bcgs/hero--@2.jpg) !default;
$insert-cta-bcg: url(../images/bcgs/insert--cta.jpg) !default;
$insert-cta-retina-bcg: url(../images/bcgs/insert--cta--@2.jpg) !default;

$slide-bcg-one: url(../images/carousel/carousel-slide-one.jpg) !default;
$slide-bcg-two: url(../images/carousel/carousel-slide-two.jpg) !default;
$slide-bcg-three: url(../images/carousel/carousel-slide-three.jpg) !default;
$slide-bcg-four: url(../images/carousel/carousel-slide-four.jpg) !default;

// Settings - Colors
$color--primary: palette-grabber($palette-material, blue, 500) !default;// blue
$color--secondary: palette-grabber($palette-material, grey, 100);
$color--tertiary: #27282d;// Very dark (mostly black) blue
$color--text: palette-grabber($palette-material, grey, 900); // Very dark grey
$color--text-light: #86878b;// Dark grayish blue
$color--black-light: #333;
$color--black-medium: #181818;
$color--grey-default: palette-grabber($palette-material, grey, 600);
