/**
 * Helper Classes
 */

/* Hide lazy loaded images in browsers not supporting JavaScript (show only fallback in <noscript> tags) */
.no-js {
  .lazy {
    display: none;
  }
}

/* Shape helpers */
.el--oval {
  border-radius: $radius--small;
}

.el--round {
  @extend .btn--round;
}

/*- Typography helpers -*/
.text--highlight {
  font-weight: bold;
  color: $color--primary;
}
