/**
 * Icons atom
 */

.icon {
  color: #a7cef7;
  cursor: pointer;

  @include transition(color);

  &:focus,
  &:hover {
    color: #fff;
  }
}

.icon--menu {
  float: right;
}

.process__icon {
  margin-top: 2rem;
  margin-bottom: 2rem;

  img {
    max-width: remy(90px);
    max-height: remy(90px);
  }
}

.process__info {
  @media screen and (max-width: 767px) {
    margin-right: auto;
    margin-left: auto;
  }
}
